/* --- Row container: FlagCounter (left) + iNat (right) --- */
.inat-flag-container {
  display: flex;
  flex-direction: row;      /* side by side */
  align-items: flex-start;  /* align tops */
  gap: 24px;                /* space between them */
  margin: 20px 0;
}

/* Mobile: stack vertically */
@media (max-width: 768px) {
  .inat-flag-container {
    flex-direction: column;
    align-items: flex-start;
    margin: 16px 0;
    gap: 16px;
  }
}

/* --- FlagCounter block --- */
.flagcounter {
  flex: 0 0 200px;          /* fixed-ish narrow column on the left */
  max-width: 220px;
}
.flagcounter img {
  max-width: 100%;
  height: auto;
  border: none;
  display: block;
}

/* --- iNaturalist widget block --- */
.inat-widget,
.inat-widget a,
.inat-label,
.inat-value,
.inat-user {
  font-family: inherit !important;
}

/* Hide the iNaturalist header/logo block (if injected) */
.inat-widget-header {
  display: none !important;
}

/* Remove borders/boxes the script adds */
.inat-widget table {
  border-collapse: collapse !important;
  width: 100%;
}
.inat-widget tr,
.inat-widget td {
  border: none !important;
  background: transparent !important;
  box-shadow: none !important;
}

/* Image niceties */
.inat-observation-image img,
.inat-user-image img {
  max-width: 48px !important;
  border-radius: 6px;
  vertical-align: middle;
}

/* Spacing between rows */
.inat-widget td {
  vertical-align: top;
  padding: 6px 0 !important;
}
